import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import pe1 from "../assets/images/pic.png";
import axios from "axios";
import { SlOptions } from "react-icons/sl";
import { Usergallerynotification } from "../components/Usergallerynotification";
import userpic from "../assets/images/pic.png";
import { DateTime } from "luxon";
function Dashboardnotification({ refresh }) {
  var apiUrlnoti = "https://backend.amourette.no/api/notifications/";
  var apiUrl = "https://backend.amourette.no/api/members/";
  const [AllfriendId, setAllfriendId] = useState([]);
  const [notificationsdetail, setnotificationsdetail] = useState([]);
  const [LoginData, setLoginData] = useState("");
  useEffect(() => {
    const getAllfriends = async () => {
      const storedUsername = localStorage.getItem("userLogin");
      const userLogin = storedUsername ? JSON.parse(storedUsername) : null;
      let formData = {
        user_id: userLogin.id,
      };
      try {
        const res = await axios.post(apiUrl + "getAllfriends", formData, {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json", // Ensure the content type is JSON
          },
        });
        var respo = res.data.results;
        const friendUserIds = respo.map((friend) => friend.id);
        setAllfriendId(friendUserIds);
        const galleries = await getnotifications(friendUserIds);
      } catch (err) {
        // Enhanced error handling
        if (err.response) {
        } else if (err.request) {
          console.error("Request data:", err.request);
        } else {
          console.error("Error message:", err.message);
        }
      }
    };
    getAllfriends();
  }, [refresh, notificationsdetail]);

  const getnotifications = async (userIds) => {
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null;

    const userIdsWithSelf = [...userIds, userLogin.id]; // e.g., [3, 1, 5]
    const userIdsString = userIdsWithSelf.join(", ");
    let formData = {
      user_ids: userIdsString, // Sending as a string for the query
      user_id: userLogin.id,
    };
    try {
      const res = await axios.post(
        apiUrlnoti + "getnotificationsdashboard",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json", // Ensure the content type is JSON
          },
        }
      );
      var respo = res.data.results;
      setnotificationsdetail(respo);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const formateTime = (dateString) => {
    if (!dateString) return null;

    // Treat input as a Europe/Oslo time (even though it has 'Z')
    // Remove the 'Z' if it exists
    const cleanDateString = dateString.replace(/Z$/, "");

    // Parse it as if it's Oslo local time (not UTC)
    const osloTime = DateTime.fromISO(cleanDateString, { zone: "Europe/Oslo" });

    if (!osloTime.isValid) {
      console.error("Invalid Oslo Time:", osloTime.invalidExplanation);
      return "Invalid Date";
    }

    // Convert to current system's local timezone
    const localTime = osloTime.setZone(
      Intl.DateTimeFormat().resolvedOptions().timeZone
    );

    const userTimeZone = Intl.DateTimeFormat().resolvedOptions().timeZone;

    console.log(osloTime);
    const now = DateTime.now().setZone(userTimeZone);
    const localPostTime = localTime.toFormat("HH:mm");

    const diffInMinutes = now.diff(localTime, "minutes").minutes;
    const diffInHours = now.diff(localTime, "hours").hours;
    const diffInDays = now.diff(localTime, "days").days;
    const diffInWeeks = Math.floor(diffInDays / 7);
    const diffInMonths = Math.floor(diffInDays / 30);
    const diffInYears = Math.floor(diffInDays / 365);

    if (diffInMinutes < 60) return `${localPostTime}`;
    if (diffInHours < 24) return `${localPostTime}`;
    if (Math.floor(diffInDays) === 1) return `Yesterday at ${localPostTime}`;
    if (diffInDays < 7) return `${Math.floor(diffInDays)}d ${localPostTime}`;
    if (diffInWeeks < 4) return `${diffInWeeks}w ${localPostTime}`;
    if (diffInMonths < 12) return `${diffInMonths}m ${localPostTime}`;
    return `${diffInYears}y ${localPostTime}`;
  };
  //Gallery popup
  const [isPopupOpen1, setIsPopupOpen1] = useState(false);
  const [seperateGallerydetail, setseperateGallerydetail] = useState("");
  const [UserDetail, setUserDetail] = useState("");
  const [GalleryCommets, setGalleryCommets] = useState([]);
  const [GalleryIdDetailGet, setGalleryIdDetailGet] = useState("");
  const [nextId, setnextId] = useState("");
  const [previousId, setpreviousId] = useState("");
  const closePopup1 = () => {
    setIsPopupOpen1(false);
    document.body.style.overflowY = "auto";
  };
  var apiUrlMember = "https://backend.amourette.no/api/members/";
  useEffect(() => {
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null;
    setLoginData(userLogin);
    getUserDetail();
  }, []);
  const getUserDetail = async () => {
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null;
    let formData = {
      user_id: userLogin.id,
    };
    try {
      const res = await axios.post(apiUrlMember + "getUserDetail", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      if (res.data.row.length > 0) {
        var respo = res.data.row[0];
        setUserDetail(respo);
      }
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const getGalleryDetail = async (galleryId) => {
    console.log(";ll");
    setGalleryIdDetailGet(galleryId);
    getGalleryComments(galleryId);
    getGalleryDetailnextprevious(galleryId);
    let formData = {
      id: galleryId,
      user_id: LoginData.id,
    };
    try {
      const res = await axios.post(
        apiUrlMember + "getGalleryDetail",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json", // Ensure the content type is JSON
          },
        }
      );
      if (res.data.row.length > 0) {
        var respo = res.data.row[0];
        openPopup1();
        setseperateGallerydetail(respo);
      }
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const getGalleryDetailnextprevious = async (galleryId) => {
    let formData = {
      id: galleryId,
      user_id: LoginData.id,
    };
    try {
      const res = await axios.post(
        apiUrlMember + "getGalleryDetailnextprevious",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json", // Ensure the content type is JSON
          },
        }
      );

      if (res.data.next_gallery_id !== null) {
        setpreviousId(res.data.next_gallery_id);
      }
      if (res.data.previous_gallery_id !== null) {
        setnextId(res.data.previous_gallery_id);
      }
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const getUserfav = async () => {
    getGalleryDetail(GalleryIdDetailGet);
  };
  const getCommentPost = async () => {
    getGalleryDetail(GalleryIdDetailGet);
  };
  const getgallerydetialnext = () => {
    getGalleryDetail(nextId);
  };
  const getgallerydetialprev = () => {
    getGalleryDetail(previousId);
  };
  const getGalleryComments = async (g_id) => {
    let formData = {
      id: g_id,
      user_id: LoginData.id,
    };
    try {
      const res = await axios.post(apiUrl + "getGalleryComments", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });

      var respo = res.data.results;

      setGalleryCommets(respo);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const openPopup1 = () => {
    setIsPopupOpen1(true);
    document.body.style.overflowY = "hidden";
  };

  return (
    <>
      <div className="d-flex flex-column gap-4 ">
        <div className="d-flex flex-column gap-3">
          {notificationsdetail.length === 0 ? ( // Check if there are no notifications
            <p></p>
          ) : (
            <div className="d-flex flex-column gap-3">
              {notificationsdetail.map((notification, index) => (
                <div className="postblock d-flex flex-column rounded-4 overflow-hidden h-100">
                  <div className="d-flex justify-content-between align-items-center h-100 px-2 py-1">
                    <div className="flex-grow-1">
                      <Link
                        to={
                          notification.link_href?.trim() === "/previewimages" &&
                          notification.post_id &&
                          notification.image_url
                            ? `/previewimages/${
                                notification.post_id
                              }/${encodeURIComponent(notification.image_url)}`
                            : notification.link_href?.trim() || "#"
                        }
                        onClick={(e) => {
                          const trimmedHref = notification.link_href?.trim();

                          // Handle /allgallery
                          if (
                            trimmedHref === "/allgallery" &&
                            notification.post_id
                          ) {
                            e.preventDefault();
                            getGalleryDetail(notification.post_id);
                          }

                          // Handle /editprofile/viewrequest with redirect
                          if (trimmedHref === "/editprofile/viewrequest") {
                            e.preventDefault();
                            window.location.href = "/editprofile/viewrequest"; // or use navigate("/editprofile/viewrequest") if using useNavigate from react-router
                          }
                        }}
                        className="d-flex gap-3"
                        key={index}
                      >
                        <div className="flex-shrink-0">
                          <div className="urpic ofit rounded-circle overflow-hidden">
                            <img
                              src={
                                notification.status === "Admin"
                                  ? require("../assets/images/thumb.jpg")
                                  : notification.recipient_profile_image || pe1
                              }
                              alt="userpic"
                            />
                          </div>
                        </div>
                        <div className="flex-grow-1">
                          <h4>
                            {notification.status === "User" && (
                              <strong>{notification.recipient_name} </strong>
                            )}
                            {notification.status === "Admin" && (
                              <strong>Admin </strong>
                            )}
                            {notification.message}
                          </h4>

                          <h5>{formateTime(notification.date)}</h5>
                        </div>
                      </Link>
                    </div>
                    {/* <div className="flex-shrink-0">
                      <button type="button" className="btnoption">
                        <SlOptions />
                      </button>
                    </div> */}
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      </div>
      <Usergallerynotification
        isOpen1={isPopupOpen1}
        onClose1={closePopup1}
        seperateGallerydetail={seperateGallerydetail}
        UserDetail={UserDetail}
        LoginData={LoginData}
        getUserfav={getUserfav}
        GalleryCommets={GalleryCommets}
        getCommentPost={getCommentPost}
        GalleryIdDetailGet={GalleryIdDetailGet}
        previousId={previousId}
        nextId={nextId}
        getgallerydetialnext={getgallerydetialnext}
        getgallerydetialprev={getgallerydetialprev}
      ></Usergallerynotification>
    </>
  );
}

export default Dashboardnotification;
